@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "AvenirNextCyr";
  font-display: swap;
  src: url("../fonts/AvenirNextCyr-Regular.woff2") format("woff2"), url("../fonts/AvenirNextCyr-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "AvenirNextCyr";
  font-display: swap;
  src: url("../fonts/AvenirNextCyr-Medium.woff2") format("woff2"), url("../fonts/AvenirNextCyr-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "AvenirNextCyr";
  font-display: swap;
  src: url("../fonts/AvenirNextCyr-Demi.woff2") format("woff2"), url("../fonts/AvenirNextCyr-Demi.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "AvenirNextCyr";
  font-display: swap;
  src: url("../fonts/AvenirNextCyr-Bold.woff2") format("woff2"), url("../fonts/AvenirNextCyr-Bold.woff") format("woff");
}
body {
  position: relative;
  min-width: 340px;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  font-family: "AvenirNextCyr", "Arial", sans-serif;
  color: #ffffff;
  background-color: #12033c;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden,
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.container {
  width: 1220px;
  max-width: 100%;
  margin: 0 auto;
}
.button {
  display: inline-block;
  padding-top: 15px;
  padding-right: 30px;
  padding-bottom: 15px;
  padding-left: 30px;
  vertical-align: middle;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  text-decoration: none;
}
.page-header {
  background-color: #12033c;
}
.page-header__row {
  padding-top: 19px;
  padding-bottom: 12px;
}
.page-header__row:not(:last-child) {
  border-bottom: 1px solid rgba(235, 235, 235, 0.5);
}
.page-header__wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-right: 10px;
  padding-left: 10px;
}
.page-header__language-preference {
  margin-right: 7.2%;
}
.page-header__phone-wrapper {
  min-width: 125px;
  margin-right: 4.5%;
}
.page-header__phone {
  position: relative;
  font-size: 14px;
  line-height: 17px;
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  text-decoration: none;
}
.page-header__phone:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 1220px) {
  .page-header__phone:hover,
  .page-header__phone:focus {
    color: #ffffff;
  }
  .page-header__phone:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
.page-header__logo img {
  min-width: 104px;
  height: 40px;
}
.page-header__logo-img {
  display: block;
}
.page-header__logo-img-color {
  display: none;
}
.page-header__link {
  font-weight: 600;
  font-size: 18px;
  line-height: 150%;
  border-bottom: 1px solid #20afff;
  color: #20afff;
  cursor: pointer;
  text-decoration: none;
}
.page-header__link:active {
  color: rgba(32, 175, 255, 0.3);
}
@media (min-width: 1220px) {
  .page-header__link:hover,
  .page-header__link:focus {
    color: rgba(32, 175, 255, 0.7);
  }
  .page-header__link:active {
    color: rgba(32, 175, 255, 0.3);
  }
}
.page-header__link:active {
  border-bottom-color: rgba(32, 175, 255, 0.3);
}
.page-header--scrolled .page-header__main-wrapper {
  position: fixed;
  z-index: 20;
  width: 100%;
  background-color: #ffffff;
  box-shadow: 0 10px 40px rgba(136, 136, 136, 0.15);
}
.page-header--scrolled .language-preference__item:not(:last-child)::after {
  color: #b9b9b9;
}
.page-header--scrolled .language-preference__link {
  color: #b9b9b9;
  cursor: pointer;
}
.page-header--scrolled .language-preference__link:active {
  color: rgba(185, 185, 185, 0.3);
}
@media (min-width: 1220px) {
  .page-header--scrolled .language-preference__link:hover,
  .page-header--scrolled .language-preference__link:focus {
    color: rgba(32, 175, 255, 0.7);
  }
  .page-header--scrolled .language-preference__link:active {
    color: rgba(32, 175, 255, 0.3);
  }
}
.page-header--scrolled .language-preference__link--selected {
  color: #20afff;
}
.page-header--scrolled .language-preference__link--selected:hover,
.page-header--scrolled .language-preference__link--selected:focus {
  color: #20afff;
}
.page-header--scrolled .page-header__phone {
  color: #b9b9b9;
  cursor: pointer;
}
.page-header--scrolled .page-header__phone:active {
  color: rgba(185, 185, 185, 0.3);
}
@media (min-width: 1220px) {
  .page-header--scrolled .page-header__phone:hover,
  .page-header--scrolled .page-header__phone:focus {
    color: rgba(32, 175, 255, 0.7);
  }
  .page-header--scrolled .page-header__phone:active {
    color: rgba(32, 175, 255, 0.3);
  }
}
.page-header--scrolled .about-company__link {
  color: #b9b9b9;
  cursor: pointer;
}
.page-header--scrolled .about-company__link:active {
  color: rgba(185, 185, 185, 0.3);
}
@media (min-width: 1220px) {
  .page-header--scrolled .about-company__link:hover,
  .page-header--scrolled .about-company__link:focus {
    color: rgba(32, 175, 255, 0.7);
  }
  .page-header--scrolled .about-company__link:active {
    color: rgba(32, 175, 255, 0.3);
  }
}
.page-header--scrolled .log-in__item:not(:last-child)::after {
  color: #b9b9b9;
}
.page-header--scrolled .log-in__link {
  color: #b9b9b9;
  cursor: pointer;
}
.page-header--scrolled .log-in__link:active {
  color: rgba(185, 185, 185, 0.3);
}
@media (min-width: 1220px) {
  .page-header--scrolled .log-in__link:hover,
  .page-header--scrolled .log-in__link:focus {
    color: rgba(32, 175, 255, 0.7);
  }
  .page-header--scrolled .log-in__link:active {
    color: rgba(32, 175, 255, 0.3);
  }
}
.page-header--scrolled .page-header__logo-img {
  display: none;
}
.page-header--scrolled .page-header__logo-img-color {
  display: block;
}
.page-header--scrolled .main-menu__link {
  color: #004460;
  cursor: pointer;
}
.page-header--scrolled .main-menu__link:active {
  color: rgba(0, 68, 96, 0.3);
}
@media (min-width: 1220px) {
  .page-header--scrolled .main-menu__link:hover,
  .page-header--scrolled .main-menu__link:focus {
    color: rgba(0, 68, 96, 0.7);
  }
  .page-header--scrolled .main-menu__link:active {
    color: rgba(0, 68, 96, 0.3);
  }
}
@media (min-width: 1220px) {
  .page-header__link:hover,
  .page-header__link:focus {
    border-bottom-color: rgba(0, 68, 96, 0.7);
  }
  .page-header__link:active {
    border-bottom-color: rgba(0, 68, 96, 0.3);
  }
  .page-header--scrolled .main-menu__link--dropdown {
    position: relative;
  }
  .page-header--scrolled .main-menu__link--dropdown::after {
    content: "";
    position: absolute;
    bottom: -11px;
    left: 48%;
    display: inline-block;
    padding: 3px;
    border: solid #004460;
    border-width: 0 1px 1px 0;
    transform: rotate(45deg);
  }
  .page-header--scrolled .main-menu__link:hover.main-menu__link--dropdown::after,
  .page-header--scrolled .main-menu__link:focus.main-menu__link--dropdown::after {
    border-color: rgba(0, 68, 96, 0.7);
  }
  .page-header--scrolled .main-menu__link:active.main-menu__link--dropdown::after {
    border-color: rgba(0, 68, 96, 0.3);
  }
}
@media (min-width: 0) and (max-width: 787px) {
  .page-header__phone-wrapper {
    min-width: 40px;
  }
  .page-header__phone {
    position: relative;
    display: flex;
    font-size: 0;
  }
  .page-header__phone::before {
    content: "";
    position: relative;
    top: 0;
    left: 0;
    display: block;
    width: 30px;
    height: 30px;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 50%;
  }
  .page-header__phone::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 32px;
    height: 32px;
    background-image: url("../img/topmenu-phone.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: 18px 18px;
  }
  .page-header--scrolled .page-header__phone::before {
    background-color: #004460;
  }
}
@media (min-width: 0) and (max-width: 1219px) {
  .page-header--scrolled .main-nav__toggle {
    background-color: #ffffff;
  }
  .page-header--scrolled .main-nav--closed .main-nav__toggle::before {
    background-color: #004460;
    box-shadow: 0 8px 0 0 #004460, 0 16px 0 0 #004460;
  }
  .page-header--scrolled .main-nav--opened .main-nav__toggle {
    background-color: #ffffff;
  }
  .page-header--scrolled .main-nav--opened .main-nav__toggle::before,
  .page-header--scrolled .main-nav--opened .main-nav__toggle::after {
    background-color: #004460;
  }
  .page-header--scrolled .main-nav--opened .main-nav__menu-wrapper {
    background-color: #ffffff;
  }
  .page-header--scrolled .main-nav--opened .main-nav__menu-wrapper::before {
    box-shadow: inset 4px 0 7px -2px rgba(185, 185, 185, 0.4);
  }
}
.language-preference {
  display: flex;
  flex-direction: row;
  margin-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
}
.language-preference__item:not(:last-child)::after {
  content: "/";
  margin-right: 7px;
  margin-left: 7px;
  color: rgba(255, 255, 255, 0.5);
}
.language-preference__link {
  font-size: 14px;
  line-height: 17px;
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  text-decoration: none;
}
.language-preference__link:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 1220px) {
  .language-preference__link:hover,
  .language-preference__link:focus {
    color: #ffffff;
  }
  .language-preference__link:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
.language-preference__link--selected {
  color: #20afff;
}
.language-preference__link--selected:hover,
.language-preference__link--selected:focus {
  color: #20afff;
}
.about-company__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}
.about-company__item {
  min-height: 22px;
}
.about-company__item:not(:last-child) {
  margin-right: 50px;
}
.about-company__link {
  font-size: 14px;
  line-height: 17px;
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  text-decoration: none;
}
.about-company__link:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 1220px) {
  .about-company__link:hover,
  .about-company__link:focus {
    color: #ffffff;
  }
  .about-company__link:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
@media (min-width: 0) and (max-width: 787px) {
  .about-company {
    display: none;
  }
}
.log-in {
  display: flex;
  flex-direction: row;
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 0;
  margin-left: auto;
  padding: 0;
  list-style: none;
}
.log-in__item:not(:last-child)::after {
  content: "|";
  margin-right: 7px;
  margin-left: 7px;
  color: rgba(255, 255, 255, 0.5);
}
.log-in__link {
  font-size: 14px;
  line-height: 17px;
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  text-decoration: none;
}
.log-in__link:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 1220px) {
  .log-in__link:hover,
  .log-in__link:focus {
    color: #ffffff;
  }
  .log-in__link:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
.main-nav__wrapper {
  margin-right: 3.5%;
}
.main-nav__toggle {
  display: none;
}
.main-nav__menu-wrapper {
  display: flex;
  flex-grow: 1;
  flex-direction: row;
}
.main-nav__inner {
  margin-left: auto;
}
@media (min-width: 0) and (max-width: 1219px) {
  .main-nav {
    position: relative;
  }
  .main-nav__menu-wrapper {
    flex-direction: column;
  }
  .main-nav__inner {
    margin-top: 40px;
    margin-left: 20px;
  }
  .main-nav__toggle {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    display: block;
    width: 70px;
    height: 45px;
    background-color: #12033c;
    border: none;
    cursor: pointer;
  }
  .main-nav__toggle:active {
    background-color: rgba(0, 153, 239, 0.6);
  }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 17px;
    width: 36px;
    height: 2px;
    background-color: #ffffff;
    box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
  }
  .main-nav--closed .main-nav__toggle:active::before {
    background-color: rgba(255, 255, 255, 0.3);
    box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.3), 0 16px 0 0 rgba(255, 255, 255, 0.3);
  }
  .main-nav--opened .main-nav__toggle {
    top: 0;
    right: auto;
    left: 0;
    background-color: #12033c;
  }
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 24px;
    left: 18px;
    width: 36px;
    height: 2px;
    background-color: #ffffff;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__toggle:active::before,
  .main-nav--opened .main-nav__toggle:active::after {
    background-color: rgba(255, 255, 255, 0.3);
  }
  .main-nav--closed .main-nav__menu-wrapper {
    display: none;
  }
  .main-nav--opened .main-nav__menu-wrapper {
    position: absolute;
    top: 0;
    left: 70px;
    z-index: 10;
    width: 250px;
    min-height: 80vh;
    background-color: #12033c;
  }
  .main-nav--opened .main-nav__menu-wrapper::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 7px;
    height: 100%;
    box-shadow: inset 4px 0 7px -2px rgba(18, 3, 60, 0.4);
    pointer-events: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__menu-wrapper {
    position: static;
